@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{
    margin: 0;
    padding: 0;
    font-size: 15px;
    color: #333;
    line-height: 26px;
    font-family: 'Open Sans', sans-serif!important;
  
}

*{
    box-sizing: border-box;
}


:root{
    --mainColor : #003471;
    --secondColor : #da7426;
}


/*****************************************/
.header{
    width: 100%;
    height: auto;
    background-color: #003471;
    background-color: var(--mainColor);
    border-top: 5px solid #da7426;
    border-top: 5px solid var(--secondColor);
}
.logo{
    width: 9rem;
}
.hicons {
    color: #fff;
    font-size: 13px;
}
.header p{
    color: #fff;
    margin: 0;
}

.header p span {
    width: 25px;
    height: 25px;
    background-color: #159ec0;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
}



.header .bgw span{
    background-color: #da7426;
    background-color: var(--secondColor);
}

.header ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;

}
.header ul li{
    margin: 0 5px;
    width: 25px;
    height: 25px;
    background-color:#159ec0;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header ul li:hover{
    background-color: #da7426;
    background-color: var(--secondColor);
    cursor: pointer;
}

.socIcons {
    font-size: 14px;
    color: #fff;
}
/********************* slider ********************/
.slider{
    width: 100%;
    height: auto;

}

.carousel {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
}

.carousel-caption {
     top: 11.25rem;
    left: 15%;
    text-align: left;
    width: 34%;
    height: auto;
}

.carousel-caption h2{
    font-size: 3rem;
    color: #fff;
    margin: 0 0 20px 0;
}

/************************ navbar ****************************/
.navbar{
    width: 100%;
    height: auto;
    position: absolute;
    left: 0;
    z-index: 999;
}



.navbar.bg-light{
    background-color: rgba(0, 0, 0, 0.4)!important;
}

.navbar-nav .nav-item{
   margin: 0 10px;
}

.navbar-nav .nav-link{
  color: #fefefd;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
}

.navbar-nav .nav-link.active {
    color: #ffaa90;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
}

.navbar-toggler {
    background-color: transparent;
   border: 1px solid #003471!important;
   border: 1px solid var(--mainColor)!important;
   border-radius: 0.25rem;
   line-height: 24px;
   transition: box-shadow .15s ease-in-out;
}

/******************** welcome ***********************/
.welcome{
    width: 100%;
    height: auto;
}

.welcome h2{
    font-size: 2.3rem;
    font-weight: 600;
    margin: 0px 0;
    color: #000000;
}

.welcome p{
    font-size: 15px;
    color: #333;
    font-weight: 500;
    margin: 25px 0;
}
.rmbtn{
   background-color: #da7426!important;
   background-color: var(--secondColor)!important;
}

.welcome img{
    max-width: 400px;
    max-height: 350px;
    object-fit: contain;
}

/******************** portfolio ***********************/
.portfolio{
    width: 100%;
    height: auto;
    background-color: #003471;
    background-color: var(--mainColor);
}

.portfolio h2{
    font-size: 2rem;
    font-weight: 600;
    margin: 0px 0;
    color: #fff;
}

.tab-pane p{
    color: #fff;
}

.card_wrap {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.card_wrap .card{
    width: 32%;
    height: auto;
    padding: 0;
    border: none;
    border-radius: 0;
    margin-bottom: 30px;
    position: relative;
    transition: 0.3;
}

.card_wrap .card:hover::after{
    content: "";
    position: absolute;
    left: 0;
    top: 11px;
    right: 0;
    width: 93%;
    height: 93%;
    margin: 0 auto;
    background-color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}


.nav-pills .nav-link {
    color: #fff!important;
    text-transform: uppercase;
    font-size: 14px;
    padding: 5px 19px;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    color: #003471!important;
    color: var(--mainColor)!important;
    background-color: #ffffff;
    padding: 5px 19px!important;
}


/************************ services ***************************/
.services{
    width: 100%;
    height: auto;
}


.services h3{
    font-size: 2.4rem;
    font-weight: 600;
    margin: 0px 0;
    color: #333;
}

.services .card{
    width: 100%;
    height: auto;
    margin-bottom: 30px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border: none;
    transition: 0.3s;
    
}

.services .card:hover{
    background-color: #da7426;
    background-color: var(--secondColor);
    color: #fff;
    cursor: pointer;
    transition: 0.3s;
}

.services .card:hover,.services .card:hover .icons,.services .card:hover h5,.services .card:hover p{
    color: #fff;
}

.services .card .card-body {
    padding: 35px 30px;
}
.subtitle{
    color: #da7426;
    color: var(--secondColor);
}
.services .icons{
    color: #1a1a19;
    font-size: 2rem;
}
.services h5{
    font-size: 1.3rem;
    font-weight: 600;
    margin: 15px 0;
    color: #1a1a19;
    text-transform: capitalize;
}
.services p{
    font-size: 16px;
    margin: 0;
    padding: 0;
    color: #8f8f8e;
}


/************************* Get in touch******************************/

.getintouch{
    width: 100%;
    height: auto;

}

.getintouch h4{
    font-size: 2.4rem;
    font-weight: 600;
    margin: 0px 0;
    color: #333;
    text-transform: capitalize;
}

.forms {
    width: 100%;
    height: auto;
    background-color: #003471;
    background-color: var(--mainColor);
    padding: 32px 48px;
}



.getintouch .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
    padding: 0 8px;
    pointer-events: none;
    border-radius: inherit;
    border-style: solid;
    border-width: 1px;
    overflow: hidden;
    min-width: 0%;
    border-color: rgb(255 255 255 / 20%);
}
.getintouch .css-14s5rfu-MuiFormLabel-root-MuiInputLabel-root {
    color: rgb(255 255 255 / 60%);
}

.map{
    width: 100%;
    height: 330px;
    display: flex;
}


/**************************** testimonial *******************************/
.testimonial{
    width: 100%;
    height: auto;
    
}

.testimonial h5{
    font-size: 2.4rem;
    font-weight: 600;
    margin: 0px 0;
    color: #333;
    text-transform: capitalize;
}

.testimonial .carousel-indicators [data-bs-target] {
   background-color: #003471;
   background-color: var(--mainColor);
   width: 25px;
   height: 25px;
   border-radius: 100%;
 
}



.testimonial .figcap{
    margin: 0;
    border: 1px solid #878787;
    padding: 15px;
    font-style: italic;
    font-size: 15px;
    font-weight: 500;
}


.testimonial .carousel-indicators {
    position: absolute;
    right: 0;
    left: auto;
    top: 0;
    padding: 0;
    margin-right: 3%;
    bottom: 0;
    align-items: center;
    margin-bottom: 0;
    flex-direction: column;
}


.testimonial .carousel-item img{
  border: 4px solid #da7426;
  border: 4px solid var(--secondColor);
}


/****************** footer **********************/
.footer{
    width: 100%;
    height: auto;
    background-color: #252525;
}

.footer h1{
    font-size: 2.2rem;
    font-weight: 600;
    margin: 0px 0;
    color: #fff;
    text-transform: capitalize;
}
.fbox{
    width: 100%;
    height: auto;

}
.fbox h5{
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    margin: 15px 0;
    color: #fff;
}

.fbox ul{
    margin: 0;
    padding: 0;
    list-style-type: none;

}

.fbox ul a{
    color: #878585;
    font-size: 15px;
    text-transform: capitalize;
    font-weight: 500;
    margin: 0;
    padding: 0;
    text-decoration: none;
    transition: all .2s ease-in-out;
}

.fbox ul a:hover{
    color: #da7426;
    color: var(--secondColor);
    font-weight: 600;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.socialIcons{
    display: flex;
    margin: 0;
    padding: 0;
}
.socialIcons li{
    margin-right:15px; 
}

.socialIcons li a {
    width: 30px;
    height: 30px;
    border-radius: 50px;
    background-color: #da7426;
    background-color: var(--secondColor);
    color: #fff;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.socialIcons li a:hover{
    background-color: #fff;
}

.footerbox{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
}

.footerbox .fboxIcon span {
    width: 50px;
    height: 50px;
    border-radius: 8px;
    background: transparent;
    border: 1px solid #da7426;
    border: 1px solid var(--secondColor);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 20px;
}
.footerbox .fboxIcon p{
    color: #fff;
    margin-bottom: 0;
    padding-left: 10px;
    font-weight: 500;
    font-size: 17px;

}

/********************* footer_bottom ************************/

.footer_bottom{
    width: 100%;
    height: auto;
    background-color: #fff;
}
.footer_bottom p{
    font-size: 15px;
    text-transform: capitalize;
    color: #333;
    font-weight: 500;
}

/********************** supper_section ***************************/
.supper_section{
    width: 100%;
    height: auto;
    
}
.heading_container h2{
    font-size: 2rem;
    font-weight: 600;
    color: #333;
    margin: 10px 0;
}
.detail-box p{
    margin: 10px 0;
    font-size: 15px;
    font-weight: 500;

}
.img-box img{
    max-width: 440px;
    max-height: 340px;
}












/* Small devices (landscape phones, 576px and up)*/
@media (max-width: 576px) { 
 .mobile button{
    margin-left: auto;
    color: #fff;
 }

 .header {
   text-align: center;
}

.header p {
     margin: 0 auto;
    padding-top: 15px;
    width: 160px;
    height: auto;
}

.header ul {
   display: flex;
    width: 160px;
    height: auto;
    margin: 0 auto;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding-top: 16px;
}

.welcome img {
     width: 100%!important;
     height: auto!important;
   
}
.respoMobile{
    flex-direction: column;
}

.testimonial .carousel-indicators {
    position: absolute;
    right: 0;
    left: 0;
    padding: 0;
    bottom: 0;
    top: auto;
    flex-direction: row;
    margin-left: 0;
    margin-right: 0;
}

.footer {
   text-align: center;
}

.fbox ul {
    justify-content: center;
}
.fbox {
   margin-bottom: 26px;
}

.footerbox {
    align-items: center;
    flex-direction: column;
    margin-bottom: 27px;
}

.footerbox .fboxIcon p {
    padding-left: 0px;
    padding-top: 6px;
}

.img-box img {
    width: 100%;
    height: auto;
}

.navbar {
    width: 100%;
    height: auto;
    position: inherit;
    left: 0;
    z-index: 999;
}

.navbar.bg-light {
    background-color: rgb(0 30 64)!important;
}
 }

/* Medium devices (tablets, 768px and up)*/
@media (max-width: 768px) { 
    .mobile button{
        margin-left: auto;
        color: #fff;
     }
 }

 /* Large devices (desktops, 992px and up)*/
@media (max-width: 992px) { 
    .mobile button{
        margin-left: auto;
        color: #fff;
     }
 }
